.post-card {
  background: #FFFFFF;
  border-radius: 20px; /* Adjusted for a larger border radius */
  margin: 10px auto; /* Center the card in the view with auto */
  padding: 16px; /* Padding inside the card */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Slightly larger shadow for depth */
  max-width: 500px; /* Maximum width of the card */
  width: 100%; /* Make it responsive */
  padding-bottom: 70px;
}

.post-header {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.user-avatar {
  width: 50px; /* Slightly larger avatar */
  height: 50px; /* Slightly larger avatar */
  border-radius: 25px; /* Keep it round */
  margin-right: 12px; /* Adjust space between avatar and username */
}

.user-details {
  display: flex;
  flex-direction: column;
}

.name-and-username {
  display: flex;
  align-items: baseline;
}

.user-real-name {
  color: var(--Primary-Dark, #1C1C1C);
  text-align: center;
  font-family: "Geist Variable";
  font-size: 18px;
  font-style: normal;
  
  line-height: normal;
  font-weight: bold;

/* Add your desired spacing here, for example: */
   margin-right: 8px;
}

.username {
  text-align: center;
  font-family: "Geist Variable";
  font-size: 6px;
  font-style: normal;
  color: #555;
  line-height: normal;
}

.post-time {
  color: #999;
  font-size: 1em;
  /* Add a top margin if you want to push it down a bit */
  margin-top: 4px;
}

.restaurant-button {
  font-size: 14px;
  padding: 5px 15px; 
  margin: 0; 
  border-radius: 15px; 
  background-color: #F3DBDA; 
  color: #EB5635; 
  border: none; 
  cursor: pointer; 
  display: inline-flex; 
  align-items: center; 
  justify-content: center; 
  text-decoration: none; 
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  font-family: "Geist Variable";
  font-weight: 600;
}

.restaurant-button .icon {
  
  margin-right: 5px; 
}

.restaurant-button a {
  text-decoration: none; 
  color: inherit; 
}


.post-title {
  font-size: 1em; /* Adjusted font size */
  font-weight: bold; /* Make title bold */
  color: #000; /* Black color for the title */
  margin-bottom: 8px; /* Space between title and description */
}

.post-description {
  color: #666; /* Dark grey color for description */
  margin-bottom: 12px; /* Space between description and image */
}

.post-image {
  border-radius: 10px; /* Rounded corners for the image */
  width: calc(100% + 32px); /* Image width larger than container to align with the card edges */
  margin-left: -16px; /* Align the image with the left edge of the card */
  margin-right: -16px; /* Align the image with the right edge of the card */
  margin-bottom: 16px; /* Space below the image */
}

.post-footer {
  display: flex;
  align-items: center;
  justify-content: start; /* Align to the start of the container */
}

.price-button {
  padding: 4px 12px;
  font-size: 0.8em;
  border-radius: 20px;
  background-color: #F3DBDA; /* Or any other color you prefer */
  color: white;
  border: none;
  cursor: pointer;
  margin-right: 8px;
  font-weight: bold;
}

.rating-button {
  padding: 4px 12px;
  font-size: 0.8em;
  border-radius: 20px;
  background-color: #F3DBDA; /* Or any other color you prefer */
  color: white;
  border: none;
  cursor: pointer;
  margin-right: 8px;
  font-weight: bold;
}

.mood-button {
  padding: 4px 12px;
  font-size: 0.8em;
  border-radius: 20px;
  background-color: #F3DBDA; /* Or any other color you prefer */
  color: white;
  border: none;
  cursor: pointer;
  margin-right: 8px;
  font-weight: bold;
}





.post-interactions {
  display: flex;
  align-items: center; /* Align interaction icons vertically */
  justify-content: space-between;
  margin-top: 8px; /* Space above interactions */
}

.likes, .comments, .shares {
  cursor: pointer;
  display: flex;
  align-items: center;
}

.restaurant-name a {
  text-decoration: none; /* Removes underline */
  color: #E74C3C; /* Specific color for restaurant names */
  /* Add any other styles specific to restaurant name links */
}

.post-link {
  text-decoration: none; /* Removes underline */
  color: inherit; /* Inherits the text color from the parent */
  /* Add any other styles you want for post links */
}


/* Add media query for responsiveness */
@media (max-width: 768px) {
  .post-card {
    margin: 10px 5px; /* Smaller side margins on mobile */
    padding: 12px; /* Smaller padding on mobile */
  }

  .post-image {
    width: calc(100% + 24px); /* Smaller total width on mobile */
    margin-left: -12px; /* Align with the left edge of the card */
    margin-right: -12px; /* Align with the right edge of the card */
  }
}